/* Reset some default styles for consistency across browsers */
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    display: flex;
    align-items: center;
    justify-content: center;
    min-height: 100vh;
    background-color: #073b4c;
    padding: 10px;
    /* Added padding for small screens */
}

/* Container for the birthday card */
.birthdayCard {
    position: relative;
    width: 80vw;
    max-width: 300px;
    height: 120vw;
    max-height: 420px;
    cursor: pointer;
    transform-style: preserve-3d;
    transform: perspective(2500px);
    transition: transform 1s, box-shadow 1s;
}

/* Hover effects */
.birthdayCard:hover {
    transform: perspective(2500px) rotate(5deg);
    box-shadow: inset 100px 20px 100px rgba(0, 0, 0, 0.2),
        0 10px 100px rgba(0, 0, 0, 0.5);
}

.birthdayCard:hover .cardFront {
    transform: rotateY(-160deg);
}

.birthdayCard:hover .happy {
    visibility: hidden;
}

/* Front side of the card */
.cardFront {
    position: absolute;
    background-color: #fff;
    width: 100%;
    height: 100%;
    overflow: hidden;
    transform-origin: left;
    box-shadow: inset 100px 20px 100px rgba(0, 0, 0, 0.2),
        30px 0 50px rgba(0, 0, 0, 0.4);
    transition: transform 0.6s;
    border-radius: 10px;
}

/* Happy Birthday heading */
.happy {
    font-family: Tahoma, sans-serif;
    text-align: center;
    margin: 5%;
    background-image: linear-gradient(120deg, #f6d365 0%, #fda085 100%);
    transition: visibility 0.1s;
    backface-visibility: visible;
    padding: 10px;
    border-radius: 5px;
    font-size: 1.2em;
}

/* Balloons container */
.balloons {
    position: absolute;
    width: 100%;
    height: 100%;
    pointer-events: none;
    /* Ensure balloons don't interfere with hover */
}

/* Balloon styles */
.balloonOne,
.balloonTwo,
.balloonThree,
.balloonFour {
    position: absolute;
    width: 15vw;
    max-width: 40px;
    height: 17vw;
    max-height: 45px;
    border-radius: 50%;
}

/* Individual balloon positions and colors */
.balloonOne {
    background-color: rgba(239, 71, 111, 0.7);
    left: 5%;
    top: 25%;
}

.balloonTwo {
    background-color: rgba(6, 214, 160, 0.7);
    left: 35%;
    top: 10%;
}

.balloonThree {
    background-color: rgba(255, 209, 102, 0.7);
    left: 65%;
    top: 25%;
}

.balloonFour {
    background-color: rgba(17, 138, 178, 0.7);
    left: 85%;
    top: 10%;
}

/* Balloon strings */
.balloonOne:before,
.balloonTwo:before,
.balloonThree:before,
.balloonFour:before {
    content: "";
    position: absolute;
    width: 0.1vw;
    height: 30vw;
    max-height: 50px;
    background-color: #ffd166;
    top: 100%;
    left: 50%;
    transform: translateX(-50%);
}

.balloonOne:after,
.balloonTwo:after,
.balloonThree:after,
.balloonFour:after {
    content: "";
    position: absolute;
    border-right: 0.3vw solid transparent;
    border-left: 0.3vw solid transparent;
    border-top: 0.3vw solid currentColor;
    top: 100%;
    left: 50%;
    transform: translateX(-50%) translateY(0.3vw);
}

.balloonOne:after {
    border-top-color: #ef476f;
}

.balloonTwo:after {
    border-top-color: #06d6a0;
}

.balloonThree:after {
    border-top-color: #ffd166;
}

.balloonFour:after {
    border-top-color: #118ab2;
}

/* Inside of the card */
.cardInside {
    position: absolute;
    background-color: #fff;
    width: 100%;
    height: 100%;
    z-index: -1;
    left: 0;
    top: 0;
    box-shadow: inset 100px 20px 100px rgba(0, 0, 0, 0.2);
    padding: 5%;
    box-sizing: border-box;
    overflow-y: auto;
    border-radius: 10px;
}

/* Back message heading */
.cardInside h3.back {
    font-family: Tahoma, sans-serif;
    color: #333;
    text-align: center;
    margin: 5% 0;
    outline-color: #333;
    outline-style: dotted;
    font-size: 1.1em;
}

/* Paragraphs inside the card */
.cardInside p {
    font-family: "Brush Script MT", cursive;
    margin: 2% 0;
    color: #333;
    line-height: 1.4;
    word-wrap: break-word;
    font-size: 1em;
}

/* Name at the bottom */
.cardInside .name {
    position: absolute;
    left: 50%;
    bottom: 5%;
    transform: translateX(-50%);
    color: #333;
    font-size: 1.2em;
    font-weight: bold;
}

/* Responsive Adjustments */

/* Landscape mode for larger screens */
@media (min-width: 600px) {
    .birthdayCard {
        width: 250px;
        height: 350px;
    }

    .happy {
        font-size: 1.2em;
    }

    .balloonOne,
    .balloonTwo,
    .balloonThree,
    .balloonFour {
        width: 40px;
        height: 45px;
    }

    .balloonOne:before,
    .balloonTwo:before,
    .balloonThree:before,
    .balloonFour:before {
        height: 50px;
    }

    .balloonOne:after,
    .balloonTwo:after,
    .balloonThree:after,
    .balloonFour:after {
        border-right: 7px solid transparent;
        border-left: 7px solid transparent;
        border-top: 7px solid currentColor;
        transform: translateX(-50%) translateY(0.7em);
    }

    .cardInside h3.back {
        font-size: 1.1em;
    }

    .cardInside p {
        font-size: 14px;
    }

    .cardInside .name {
        font-size: 16px;
    }
}

/* Smaller devices */
@media (max-width: 400px) {
    .birthdayCard {
        width: 90vw;
        height: 160vw;
    }

    .happy {
        font-size: 1em;
    }

    .balloonOne,
    .balloonTwo,
    .balloonThree,
    .balloonFour {
        width: 12vw;
        height: 17vw;
    }

    .balloonOne:before,
    .balloonTwo:before,
    .balloonThree:before,
    .balloonFour:before {
        height: 30vw;
    }

    .balloonOne:after,
    .balloonTwo:after,
    .balloonThree:after,
    .balloonFour:after {
        border-right: 0.3vw solid transparent;
        border-left: 0.3vw solid transparent;
        border-top: 0.3vw solid currentColor;
        transform: translateX(-50%) translateY(0.3vw);
    }

    .cardInside h3.back {
        font-size: 1em;
    }

    .cardInside p {
        font-size: 0.9em;
    }

    .cardInside .name {
        font-size: 1em;
    }
}